import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { CardActionArea, Card, CardContent } from '@mui/material';
import { Navigate } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import AdminNav from './AdminNav';
import {
  Box,
  Typography,
  Button,
  Container,
  TextField,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  TextareaAutosize,
} from '@mui/material';

const styles = {
  container: {
    maxWidth: '500px',
    margin: '0 auto',
    padding: '16px',
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: '16px',
  },
  label: {
    fontWeight: 'bold',
  },
  input: {
    padding: '12px',
  },
  select: {
    padding: '12px',
  },
  button: {
    backgroundColor: 'blue',
    color: 'white',
    padding: '12px',
    borderRadius: '5px',
    cursor: 'pointer',
  },
  card: {
    marginBottom: '16px',
  },
};

const AddMovies = () => {
  const [movieData, setMovieData] = useState({
    MovieName: '',
    Image: '',
    Category: 'UA',
    Languages: [],
    Cast: '', // New field
    Description: '', // New field
    TicketRates: 0, // New field
    NoOfSeats: 0, // New field
  });
  const [jwtToken, setJwtToken] = useState('eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhZG1pbklkIjoiNjUwYWY5MTRiODI4NGJmMmZiYWJkY2JiIiwiaWF0IjoxNjk2ODY0MTAwLCJleHAiOjE2OTY5NTA1MDB9.FUtUWPIwjFmQ2tK3b4pJWOMOF6l3ZLH6eGtS1b6ZFQM'); // Replace with your JWT token
localStorage.setItem('adminToken', jwtToken);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setMovieData({
      ...movieData,
      [name]: value,
    });
  };

  const handleLanguagesChange = (e) => {
    const selectedLanguages = Array.from(
      e.target.selectedOptions,
      (option) => option.value
    );

    setMovieData({
      ...movieData,
      Languages: selectedLanguages,
    });
  };

  const navigate = useNavigate(); // Initialize the navigate function
  const handleSubmit = (e) => {
    e.preventDefault();

    console.log('Movie Data:', movieData);
    axios
      .post('http://localhost:3001/api/addMovie', movieData, {
        headers: {
          Authorization: `Bearer ${jwtToken}`,
        },
      })
      .then((response) => {
        if (response.status === 200) {
          console.log('Movie added successfully');
          window.alert('Movie added successfully');
          const userId = response.data.data._id;
          const userName = response.data.data.Name;
          const email = response.data.data.email;
          const token = response.data.token; // Get the JWT token from the response

          console.log(token)
          const addedMovie = response.data;
          sessionStorage.setItem("userId", userId);
          sessionStorage.setItem("userName", userName);
          sessionStorage.setItem("email", email);
          sessionStorage.setItem("token", token);
         
          console.log('Navigating to /admin');
         navigate('/admin')
         
        } else {
          console.log('Error adding movie. Please try again.');
        }
      })
      .catch((error) => {
        console.error('Error adding movie:', error);
      });
  };

  const handleDelete = () => {
    // Make a DELETE request to delete the movie by ID
    axios
      .delete(`/api/deleteMovie/${movieData._id}`)
      .then((response) => {
        if (response.status === 200) {
          console.log('Movie deleted successfully');
          // Clear the movie data after deletion if needed
          setMovieData({
            MovieName: '',
            Image: '',
            Category: 'UA',
            Languages: [],
            Cast: '',
            Description: '',
            TicketRates: 0,
            NoOfSeats: 0,
          });
        } else {
          console.log('Error deleting movie. Please try again.');
        }
      })
      .catch((error) => {
        console.error('Error deleting movie:', error);
      });
  };

  return (
  <div>
     <AdminNav/>
       <div className="add" style={{ backgroundImage: 'url("https://thumbs.dreamstime.com/b/paper-texture-background-scrapbooking-artistic-design-digital-any-other-design-54498672.jpg")', backgroundSize: 'cover', backgroundRepeat: 'no-repeat', height: '1000px' }}>
    <div className="container mt-5">
  <h2>Add a New Movie</h2>
  <form onSubmit={handleSubmit}>
    <div className="mb-3">
      <label htmlFor="MovieName" className="form-label">
        Movie Name:
      </label>
      <input
        type="text"
        id="MovieName"
        name="MovieName"
        value={movieData.MovieName}
        onChange={handleChange}
        className="form-control"
        required
      />
    </div>
    <div className="mb-3">
      <label htmlFor="Image" className="form-label">
        Image URL:
      </label>
      <input
        type="text"
        id="Image"
        name="Image"
        value={movieData.Image}
        onChange={handleChange}
        className="form-control"
        required
      />
    </div>
    <div className="mb-3">
      <label htmlFor="Category" className="form-label">
        Category:
      </label>
      <select
        id="Category"
        name="Category"
        value={movieData.Category}
        onChange={handleChange}
        className="form-select"
        required
      >
        <option value="UA">UA</option>
        <option value="A">A</option>
        <option value="PG">PG</option>
      </select>
    </div>
    <div className="mb-3">
      <label htmlFor="Languages" className="form-label">
        Languages:
      </label>
      <select
        id="Languages"
        name="Languages"
        multiple
        value={movieData.Languages}
        onChange={handleLanguagesChange}
        className="form-select"
        required
      >
        <option value="Malayalam">Malayalam</option>
        <option value="English">English</option>
        <option value="Hindi">Hindi</option>
        <option value="Tamil">Tamil</option>
        <option value="Telugu">Telugu</option>
      </select>
    </div>
    <div className="mb-3">
      <label htmlFor="Cast" className="form-label">
        Cast:
      </label>
      <input
        type="text"
        id="Cast"
        name="Cast"
        value={movieData.Cast}
        onChange={handleChange}
        className="form-control"
        required
      />
    </div>
    <div className="mb-3">
      <label htmlFor="Description" className="form-label">
        Description:
      </label>
      <textarea
        id="Description"
        name="Description"
        value={movieData.Description}
        onChange={handleChange}
        className="form-control"
        required
      ></textarea>
    </div>
    <div className="mb-3">
      <label htmlFor="TicketRates" className="form-label">
        Ticket Rates:
      </label>
      <input
        type="number"
        id="TicketRates"
        name="TicketRates"
        value={movieData.TicketRates}
        onChange={handleChange}
        className="form-control"
        required
      />
    </div>
    <div className="mb-3">
      <label htmlFor="NoOfSeats" className="form-label">
        Number of Seats:
      </label>
      <input
        type="number"
        id="NoOfSeats"
        name="NoOfSeats"
        value={movieData.NoOfSeats}
        onChange={handleChange}
        className="form-control"
        required
      />
    </div>
    <button type="submit" className="btn btn-primary">
      Add Movie
    </button>
  </form>
</div>
  </div>
  </div>
  )
}

export default AddMovies